import React, { useEffect } from "react";
import { TourInland } from "src/constant/Array";
import "../home.css";
import TourInlandImages from "src/assets/Home/TourInland";
import { useLocation, useNavigate } from "react-router-dom";

const Tour_Inland = ({ aos,ref, data }: any) => {
  const navigate = useNavigate();
  const location = useLocation();
  const state = location.state; // Lấy state được truyền
  const id = state?.id;
  const type = state?.type

  useEffect(() => {
    if (id && type == 'DU_LICH_NOI_DIA') {
      const element = document.querySelector(`#${id}`);
      if (element) {
        element.scrollIntoView({ behavior: 'smooth' });
      }
    }
  }, [location.state]);

  return (
    <div id="du-lich-noi-dia" data-aos={aos} ref={ref} className="container-xxl">
      <div
        className="d-flex justify-content-center flex-column"
        style={{
          width: "max-content",
        }}
      >
        <div className="title-body-2">Tour nội địa</div>
        <div
          style={{
            height: 3,
            backgroundColor: "#00b09b",
          }}
        />
      </div>
      <div className="row mt-4">
        {data.map((item: any) => {
          return (
            <div key={item.id} className="col-6 col-md-6 col-lg-3 mb-4">
              <button
                onClick={() => {
                  if (item.content) {
                    navigate(`/blog/${item.id}`, {
                      state: { content: item.content },
                    });
                  } else {
                  }
                }}
                className="item-tour-inland"
              >
                <div className="d-flex align-items-center flex-column">
                  <img
                    src={item.thumbnail}
                    className="img-item-tour-inland"
                    
                  />
                  <div className="title-item-tour-inland">{item.title}</div>

                  <button className="btn-item-tour-inland">Xem chi tiết</button>
                </div>
              </button>
            </div>
          );
        })}
      </div>
      <div className="d-flex align-items-center justify-content-center my-4">
        <button className="btn-see-all-tour-inland">Xem tất cả</button>
      </div>
    </div>
  );
};

export default Tour_Inland;
